const app = getApp();
import helper from '../common/helper.js';
export default {
	data() {
		return {
			list: [],
			imgUrl: '',
			page: '1',
			indexType: ''
		}
	},
	onLoad(e) {
		console.log(e)
		this.imgUrl = helper.imgUrl;
		this.indexType = e.index
		let name = ''
		if(this.indexType == 'one'){
			name = '一级代理',
			this.getTeamOne()
		} else if(this.indexType == 'two'){
			name = '二级代理',
			this.getTeamTwo()
		}
		uni.setNavigationBarTitle({
		    title: name
		});
	},
	methods: {
		// 打电话
		getTel(e){
			uni.makePhoneCall({
			    phoneNumber: e //仅为示例
			});
		},
		getDetail(i){
			let index = i.currentTarget.dataset.index
			let itemNew = JSON.stringify(i.currentTarget.dataset.item)
			uni.navigateTo({
				url:`/pages/teamPeopleDetail?itemDetail=${itemNew}&index=${index}`
			})
		},
		getTeamOne(){
			uni.request({
				url: `${helper.requestUrl}/user/team-list-one`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					page: this.page,
					page_size: '20'
				},
				success: res => {
					// uni.hideLoading();
					res = helper.null2str(res);
					console.log(res,'8');
					if (res.statusCode == 200) {
						this.list = res.data.datas.one.data
					} else {
						// uni.showToast({
						// 	title: res.data.message
						// });
					}
				}
			});
		},
		getTeamTwo(){
			uni.request({
				url: `${helper.requestUrl}/user/team-list-two`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					page: this.page,
					page_size: '20'
				},
				success: res => {
					// uni.hideLoading();
					res = helper.null2str(res);
					console.log(res,'8');
					if (res.statusCode == 200) {
						this.list = res.data.datas.two.data
					} else {
						// uni.showToast({
						// 	title: res.data.message
						// });
					}
				}
			});
		},
		onReachBottom(){
			this.page ++;
			this.getTeamOne()
			this.getTeamTwo()
		}
	}
}
